/*eslint-disable*/
import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// nodejs library that concatenates classes
import classNames from "classnames";
import { List, ListItem, withStyles } from "@material-ui/core";

// @material-ui/icons
import Favorite from "@material-ui/icons/Favorite";

import footerStyle from "assets/jss/material-kit-react/components/footerStyle.jsx";
import './style.css'

function Footer({ ...props }) {
  const { classes, whiteFont } = props;
  const footerClasses = classNames({
    [classes.footer]: true,
    [classes.footerWhiteFont]: whiteFont
  });
  const aClasses = classNames({
    [classes.a]: true,
    [classes.footerWhiteFont]: whiteFont
  });
  return (
    <footer className="footer">
   
      <div className="row">
          <a href="https://www.gatsbyjs.org/docs/" className="col">
            <p className="icon">
              <Favorite/>
            </p>
            <p id="text">Docss</p>
          </a>
          <a href="https://www.gatsbyjs.org/tutorial/" className="col">
            <p className="icon">
             <Favorite/>
            </p>            
            <p className="text">Tutorial</p>
          </a>
          <a href="https://www.gatsbyjs.org/plugins/" className="col">
            <p className="icon">
             <Favorite/>
            </p>
            <p>plugins</p>
          </a>
          <a href="https://www.gatsbyjs.org/blog/" className="col">
            <p className="icon">
              <Favorite/>
            </p>           
            <p>Blog</p>
          </a>
          <a href="https://www.gatsbyjs.org/showcase/" className="col">
            <p className="icon">
              <Favorite/>
            </p>
            <p>Showcase</p>
          </a>
      </div>
    </footer>
  );
}

Footer.propTypes = {
  classes: PropTypes.object.isRequired,
  whiteFont: PropTypes.bool
};

export default withStyles(footerStyle)(Footer);
